import { random } from "../random";
import { ENGLISH, CHINESE } from "./LanguageEnum";

// prettier-ignore
const generate = (language) => {
	let eq = "";
	const answers = Array(4);

	let s_price = 0;
	let gain_pc = 0;
	let loss_pc = 0;
	let ans = [];

	do {
		s_price = (random(21)+10)*10;
		gain_pc = (random(7)+1)*10;
		loss_pc = (random(3)+1)*10;

		ans[0] = Math.trunc(s_price*gain_pc/(100+gain_pc)-Math.trunc(s_price)*loss_pc/(100-loss_pc));
		ans[1] = Math.trunc(s_price)*(gain_pc-loss_pc)/100;
		ans[2] = random(100)+1; if (random(2)===0) ans[2] = -ans[2];
		ans[3] = Math.trunc(s_price*2*((100+gain_pc)/100*(100-loss_pc)/100-1));
	} while (ans[0]===ans[1] || ans[0]===ans[2] || ans[0]===ans[3] || ans[1]===ans[2] || ans[1]===ans[3] || ans[2]===ans[3]);

	if (language===ENGLISH) {
		eq = "\\text{Peter sold two bags for }\\$"+s_price+"\\text{ each. He gained }"+gain_pc+"\\%\\text{ on one and lost }"+loss_pc+"\\%\\text{ on the other. After the two transactions, Peter}";
	} else if (language===CHINESE) {
		eq = "\\text{ }\\$"+s_price+"\\text{ }\\\\\\text{ }"+gain_pc+"\\%\\text{  }"+loss_pc+"\\%\\text{ }";
	}

	for (let i=0; i<ans.length; i++) {
		if (ans[i]>0) {
			if (language===ENGLISH) answers[i] = "\\text{gained }\\$"+ans[i]+"\\text{.}"; else if (language===CHINESE) answers[i] = "\\text{ }\\$"+ans[i]+"\\text{ }";
		} else if (ans[i]<0) {
			if (language===ENGLISH) answers[i] = "\\text{loss }\\$"+Math.abs(ans[i])+"\\text{.}"; else if (language===CHINESE) answers[i] = "\\text{ }\\$"+Math.abs(ans[i])+"\\text{ }";
		} else if (ans[i]===0) {
			if (language===ENGLISH) answers[i] = "\\text{had no gain and no loss.}"; else if (language===CHINESE) answers[i] = "\\text{}";
		}
	}

	const answerList = answers.map((v, i) => ({ text: v, isCorrect: i === 0 }));
	const mathQ = { equationLatex: eq, answers: answerList, isMultipleChoice: answerList.filter(e => e.isCorrect).length > 1 };

	return mathQ;
};

const MathQuestionGeneratorType14 = {
	generate,
};
export default MathQuestionGeneratorType14;
export { MathQuestionGeneratorType14 };
